*{box-sizing:border-box}html{font-size:62.5%;background-color:#282c35;padding-bottom:20px}body{color:#282c35;font-family:'Nunito',Arial, sans-serif;font-size:1.6rem;line-height:1.6}button{cursor:pointer}button:disabled{cursor:default}.is-active{font-weight:bold}a{padding:0}.inlineMessage__success{display:inline;color:#5cb85c}.box-layout{align-items:center;background-image:url("/images/bg.jpg");background-size:cover;display:flex;height:100vh;flex-direction:column;justify-content:center;width:100vw}.box-layout__box{background:rgba(255,255,255,0.85);border-radius:3px;text-align:center;padding:3.2rem 1.6rem;width:35rem}.box-layout__title{margin:0 0 1.6rem 0;line-height:1}.button{font-size:2.4rem;background:#1c88bf;border:none;display:inline-block;text-decoration:none;line-height:1;font-weight:300;-webkit-border-radius:50px;-moz-border-radius:50px;border-radius:50px;color:#ffa7c4;padding:1.2rem;margin:10px 10px 0 0}.button--link{background:none}.button--secondary{background:#a5afd7}.button--darkpink{background:#333745}.button--greypink{background:#282c35}.button-pinkwhite{background:#ffa7c4;color:white}.button--black-text{color:black}.button-small{font-size:1.4rem;margin:5px 5px 0 10px}.header__title{color:white;text-decoration:none}.header__title h1{margin:0}.header-content{align-items:center;display:flex;justify-content:space-between;padding:1.2rem 0}.content-container{margin:0 auto;padding:0 1.6rem;max-width:40%;color:white}.content-container__blog img{max-width:100%;max-height:100%;min-width:auto;min-height:auto}.page-header{background:#333745;margin:0 auto;padding:3.2rem 0;color:white;max-width:40%;border-radius:30px}.page-header .content-container{margin:0 20px;max-width:100%}.page-header__title{font-size:4.8rem;font-weight:lighter;margin:0}.page-header__title span{font-weight:700}.page-header__actions{margin-top:1.6rem}.input-group{padding:0;margin:10px 0 0 0}.input-group input{background:#282c35;border:1px solid grey;color:#ffa7c4;display:inline-block;padding:5px;margin-left:10px}.input-group label{display:inline-block;padding:5px;background:#333745;border-radius:20px;color:#ffa7c4}.text-input,.select,.textarea{border:1px solid #cacccd;height:50px;padding:1.2rem;font-size:2.4rem;font-weight:300}.select{padding:10px}.textarea{height:10rem}.input--darkpink{background:#333745;color:#ffa7c4;border:none;display:inline-block;padding:5px}.form{display:flex;flex-direction:column}.form>*{margin-bottom:1.6rem}.form__error{margin:0 0 1.6rem 0;font-style:italic}@media (min-width: 1397px){.show-for-mobile{display:none}}@media (max-width: 1396px){.show-for-desktop{display:none}}.list-header{background-color:#f7f7f7;border:1px solid #e5e5e5;color:#666;display:flex;justify-content:space-between;padding:1.2rem 1.6rem}.list-body{margin-bottom:1.6rem}@media (min-width: 1397px){.list-body{margin-bottom:3.2rem}}.list-item{box-sizing:border-box;color:white;text-decoration:none;transition:background .3s ease;display:block;padding:25px;margin-bottom:10px;border-radius:50px;border:0.2px solid #282c35}.list-item:hover{background:#22242e}.list-item--message{align-items:center;color:#666;justify-content:center;padding:1.6rem}.list-item--message:hover{background:none}.list-item__title{margin:0;word-break:break-all;color:#ffa7c4;font-size:2.4rem}.list-item__subtitle{color:#666;font-size:1.4rem}.list-item__data{display:flex;flex-direction:row;justify-content:space-between}@media (min-width: 1397px){.list-item__data{margin:0;padding-left:1.2rem}}.link-item{border-top:none;color:white;text-decoration:none;padding:1.2rem}.loader{display:flex;justify-content:center;align-items:center;margin-top:100px}.loader__img{height:6rem;width:6rem}.editor-border{border:1px solid #666;padding:10px;border-radius:10px}
